import { Component, inject, TemplateRef } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Chart } from 'chart.js/auto';
import { ReportService } from 'src/app/core/services/report/report.service';
import ChartDataLabels from 'chartjs-plugin-datalabels';

@Component({
  selector: 'app-trail-balance-report',
  templateUrl: './trail-balance-report.component.html',
  styleUrls: ['./trail-balance-report.component.scss'],
})
export class TrailBalanceReportComponent {
  myForm: FormGroup;
  submitted: boolean = false;
  trailBalance: any = {};
  year = '2024';
  private modalService = inject(NgbModal);
  public chart: any;
  years = ['2024', '2023', '2022', '2021', '2020'];
  monthNames = [
    'Jan',
    'Feb',
    'Mar',
    'Apr',
    'May',
    'Jun',
    'Jul',
    'Aug',
    'Sep',
    'Oct',
    'Nov',
    'Dec',
  ];

  constructor(
    private formBuilder: FormBuilder,
    private reportService: ReportService
  ) {
    Chart.register(ChartDataLabels);
    this.myForm = this.formBuilder.group({
      fromDate: [''],
      toDate: [''],
    });
    this.getTrailBalance();
  }
  getTrailBalance(submit?: boolean, year?: string) {
    if (year) this.year = year;
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      strStartDate: this.myForm.value.fromDate,
      strEndDate: this.myForm.value.toDate,
      year: this.year,
    };

    this.reportService.getTrailBalanceReport(obj).subscribe((res) => {
      submit ? (this.submitted = true) : (this.submitted = false);
      if (res.success) {
        this.trailBalance = res.data[0];

        if (submit) {
          this.chart.data.datasets = [
            {
              label: 'Selling Price',
              data: this.trailBalance.graphData.map(
                (item) => item.SellingPrice
              ),
              backgroundColor: '#FECACA',
            },
            {
              label: 'Total Profit',
              data: this.trailBalance.graphData.map((item) => item.TotalProfit),
              backgroundColor: '#bbf7d0',
            },
          ];
          this.chart.update();
        } else {
          setTimeout(() => {
            this.createChart();
          }, 100);
        }
      } else {
        this.trailBalance = [];
      }
    });
  }

  createChart() {
    this.chart = new Chart('MyChart', {
      type: 'bar',
      data: {
        labels: this.trailBalance.graphData.map(
          (item) => `${this.monthNames[item.month - 1]}`
        ),
        datasets: [
          {
            label: 'Selling Price',
            data: this.trailBalance.graphData.map((item) => item.SellingPrice),
            backgroundColor: '#FECACA',
          },
          {
            label: 'Total Profit',
            data: this.trailBalance.graphData.map((item) => item.TotalProfit),
            backgroundColor: '#bbf7d0',
          },
        ],
      },
      options: {
        responsive: true,
        plugins: {
          datalabels: {
            anchor: 'end',
            align: 'top',
            formatter: function (value: any) {
              return value !== 0 ? value : '';
            },
            color: 'black',
          },
          legend: {
            position: 'top',
          },
          title: {
            display: true,
            text: 'Trail Balance',
          },
        },
      },
    });
  }
  open(content: TemplateRef<any>) {
    this.modalService.open(content, {
      ariaLabelledBy: 'modal-basic-title',
      size: 'lg',
    });
    this.convertToPDF();
  }
  public convertToPDF() {
    setTimeout(() => {
      let printArea = document.getElementById('printArea');
      if (printArea) {
        let originalBody = document.body;
        let bodyElement = document.createElement('body');
        bodyElement.appendChild(printArea);
        document.body = bodyElement;
        window.print();
        document.body = originalBody;
      }
      this.modalService.dismissAll();
    }, 100);
  }
}
