import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { NgSelectModule } from '@ng-select/ng-select';
import { TranslateModule } from '@ngx-translate/core';
import { InlineSVGModule } from 'ng-inline-svg-2';
import { GrossIncomeComponent } from './gross-income/gross-income.component';
import { MisReportComponent } from './mis-report/mis-report.component';
import { PayableReportComponent } from './payable-report/payable-report.component';
import { PnlReportComponent } from './pnl-report/pnl-report.component';
import { ProcessingChargeComponent } from './processing-charge/processing-charge.component';
import { ReceivableReportComponent } from './receivable-report/receivable-report.component';
import { ReportComponent } from './report.component';
import { TrailBalanceReportComponent } from './trail-balance-report/trail-balance-report.component';

@NgModule({
  declarations: [
    ReportComponent,
    MisReportComponent,
    PnlReportComponent,
    ReceivableReportComponent,
    PayableReportComponent,
    ProcessingChargeComponent,
    GrossIncomeComponent,
    TrailBalanceReportComponent,
  ],
  imports: [
    CommonModule,
    TranslateModule,
    FormsModule,
    ReactiveFormsModule,
    InlineSVGModule,
    NgSelectModule,
    RouterModule.forChild([
      {
        path: 'mis-report',
        component: MisReportComponent,
      },
      // {
      //   path: 'customer-bill',
      //   component: CustomerBillComponent,
      // },
      {
        path: 'pnl-report',
        component: PnlReportComponent,
      },
      {
        path: 'receivable-report',
        component: ReceivableReportComponent,
      },
      {
        path: 'payable-report',
        component: PayableReportComponent,
      },
      {
        path: 'gross-income',
        component: GrossIncomeComponent,
      },
      {
        path: 'processing-charges',
        component: ProcessingChargeComponent,
      },
      {
        path: 'trail-balance-report',
        component: TrailBalanceReportComponent,
      },
    ]),
  ],
})
export class ReportModule {}
