import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import { ReportService } from 'src/app/core/services/report/report.service';
import { SupplierService } from 'src/app/core/services/supplier/supplier.service';

@Component({
  selector: 'app-processing-charge',
  templateUrl: './processing-charge.component.html',
  styleUrls: ['./processing-charge.component.scss'],
})
export class ProcessingChargeComponent implements OnInit {
  arrCustomer: any = [];
  arrTransferMode: any = [];

  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  myForm: FormGroup;

  constructor(
    private reportService: ReportService,
    private supplierService: SupplierService,
    private formBuilder: FormBuilder,
    private exportToExcelService: ExportToExcelService,
    private pageServiceObj: PagerService
  ) {
    this.myForm = this.formBuilder.group({
      customer: [null],
      fromDate: [''],
      toDate: [''],
      mode: [null],
      paymentStatus: ['ALL'],
    });
  }

  ngOnInit() {
    this.getAllIncomeReport();
    this.getMetaData();
  }
  getMetaData() {
    const obj = {
      metaType: 'transfermod', // Amentities transfermod
    };

    this.supplierService.metaData(obj).subscribe((data) => {
      if (data.success) {
        this.arrTransferMode = data.data.filter((e) => {
          return e.percentage > 0;
        });
      } else {
        this.arrTransferMode = [];
      }
    });
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllIncomeReport();
  }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrCustomer.length
    );
  }
  getAllIncomeReport() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      strStartDate: this.myForm.value.fromDate,
      strEndDate: this.myForm.value.toDate,
      modeName: this.myForm.value.mode ? this.myForm.value.mode : 'ALL',
      paymentType: this.myForm.value.paymentStatus,
    };

    this.fetchLoading = true;
    this.reportService.getProcessingChargesReport(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrCustomer = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrCustomer = [];
        // alert(res.message);
      }
    });
  }

  downloadExcel() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
      strStartDate: this.myForm.value.fromDate,
      strEndDate: this.myForm.value.toDate,
      modeName: this.myForm.value.mode ? this.myForm.value.mode : 'ALL',
      paymentType: this.myForm.value.paymentStatus,
    };

    this.fetchLoading = true;
    this.reportService.getProcessingChargesReport(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        let jsonData: any = [];

        res.data.forEach((e) => {
          let obj: any = {
            Date: moment(e.verifyDate).format('DD-MM-YYYY'),
            'Customer Code': e.cust_Code,
            'Customer Name': e.customer_name,
            Package: e.packageName,
            'Payment Mode': e.modePayment,
            'Transfer %': e.tranferPercent,
            Bank: e.bankName,
            'Service Charge': e.totalDiffrenceAmount,
            'Paid Amount': e.actualAmount,
          };
          jsonData.push(obj);
        });

        this.exportToExcelService.exportAsExcelFile(
          jsonData,
          'Processing Charge Report '
        );
      }
    });
  }
}
