import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import * as moment from 'moment';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import { ReportService } from 'src/app/core/services/report/report.service';

@Component({
  selector: 'app-gross-income',
  templateUrl: './gross-income.component.html',
  styleUrls: ['./gross-income.component.scss'],
})
export class GrossIncomeComponent implements OnInit {
  arrCustomer: any = [];

  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  myForm: FormGroup;

  constructor(
    private reportService: ReportService,
    private formBuilder: FormBuilder,
    private exportToExcelService: ExportToExcelService,
    private pageServiceObj: PagerService
  ) {
    this.myForm = this.formBuilder.group({
      customer: [null],
      fromDate: [''],
      toDate: [''],
      paymentStatus: ['ALL'],
    });
  }

  ngOnInit() {
    this.getAllIncomeReport();
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllIncomeReport();
  }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrCustomer.length
    );
  }
  getAllIncomeReport() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      strStartDate: this.myForm.value.fromDate,
      strEndDate: this.myForm.value.toDate,
      paymentType: this.myForm.value.paymentStatus,
      // fkCustomerId: '',
      // cust_Code: this.myForm.value.customer,
      // fromDate: this.myForm.value.fromDate,
      // endDate: this.myForm.value.toDate,
    };

    this.fetchLoading = true;
    this.reportService.getGrossIncomeReport(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrCustomer = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrCustomer = [];
        // alert(res.message);
      }
    });
  }

  downloadExcel() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
      strStartDate: this.myForm.value.fromDate,
      strEndDate: this.myForm.value.toDate,
      paymentType: this.myForm.value.paymentStatus,
    };

    this.fetchLoading = true;
    this.reportService.getGrossIncomeReport(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        let jsonData: any = [];

        res.data.forEach((e) => {
          let obj: any = {
            Date: moment(e.date, 'YYYY-MM-DD').format('DD-MM-YYYY'),
            'Customer Code': e.customer_code,
            'Customer Name': e.customer_name,
            Package: e.package_name,
            Supplier: e.supplier_name,
            Receivable: e.customerBalence,
            Payable: e.supplierBalence,
            Profit: e.profit,
            'Profit Release Date': e.profit_Release
              ? moment(e.profit_Date).format('DD-MM-YYYY')
              : '',
            'Profit Release': e.profit_Release ? 'Yes' : 'No',
          };
          jsonData.push(obj);
        });

        this.exportToExcelService.exportAsExcelFile(
          jsonData,
          'Gross Income Report '
        );
      }
    });
  }
}
