import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { ActivatedRoute } from '@angular/router';
import * as moment from 'moment';
import { ExportToExcelService } from 'src/app/core/services/common/export-to-excel.service';
import { CustomerService } from 'src/app/core/services/customer/customer.service';
import { PagerService } from 'src/app/core/services/pagination/pager.service';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-receivable-report',
  templateUrl: './receivable-report.component.html',
  styleUrls: ['./receivable-report.component.scss'],
})
export class ReceivableReportComponent implements OnInit {
  arrCustomer: any = [];

  // pagination
  pager: any = {};
  intTotalCount = 0;
  intPageLimit = 50;
  intSkipCount = 0;
  fetchLoading = false;

  status = '';

  myForm: FormGroup;

  constructor(
    private customerService: CustomerService,
    private formBuilder: FormBuilder,
    private exportToExcelService: ExportToExcelService,
    private activatedRoute: ActivatedRoute,
    private pageServiceObj: PagerService
  ) {
    activatedRoute.queryParams.subscribe((params) => {
      this.status = params['status'];
    });

    this.myForm = this.formBuilder.group({
      customer: [null],
      fromDate: [''],
      toDate: [''],
      paymentStatus: [this.status ? this.status : 'ALL'],
    });
  }

  ngOnInit() {
    this.getAllCustomer();
  }

  setPage(page) {
    if (page < 1 || page > this.pager.totalPages) {
      return;
    }
    this.pager = this.pageServiceObj.getPager(
      this.intTotalCount,
      page,
      this.intPageLimit
    );
    this.getAllCustomer();
  }

  getFromCount() {
    return this.intPageLimit * (this.pager.currentPage - 1) + 1;
  }

  getToCount() {
    return (
      this.intPageLimit * (this.pager.currentPage - 1) + this.arrCustomer.length
    );
  }
  getAllCustomer() {
    let skipCount = this.intSkipCount;
    if (this.pager.intSkipCount) {
      skipCount = this.pager.intSkipCount;
    }
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: skipCount,
      intPageLimit: this.intPageLimit,
      fkCustomerId: '',
      cust_Code: this.myForm.value.customer,
      fromDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
      paymentType: this.myForm.value.paymentStatus,
    };

    this.fetchLoading = true;
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        this.arrCustomer = res.data;

        this.intTotalCount = res.count;

        this.pager = this.pageServiceObj.getPager(
          this.intTotalCount,
          this.pager.currentPage,
          this.intPageLimit
        );
      } else {
        this.arrCustomer = [];
        // alert(res.message);
      }
    });
  }

  downloadExcel() {
    const obj = {
      strLoginUserId: localStorage.getItem('userId'),
      intSkipCount: 0,
      intPageLimit: 1000000,
      fkCustomerId: '',
      cust_Code: this.myForm.value.customer,
      fromDate: this.myForm.value.fromDate,
      endDate: this.myForm.value.toDate,
      paymentType: this.myForm.value.paymentStatus,
    };

    this.fetchLoading = true;
    this.customerService.getAllCustomer(obj).subscribe((res) => {
      this.fetchLoading = false;
      if (res.success) {
        let jsonData: any = [];

        res.data.forEach((e) => {
          let obj: any = {
            'Customer Code': e.cust_Code,
            'Customer Name': e.strName,
            'Created On': moment(e.createdAt).format('DD-MM-YYYY'),
            Package: e.strPackage,
            'Total Amount': e.Total_Amount,
            'Received Amount': e.Total_Paid_Amount,
            'Balance Amount': e.Total_Amount - e.Total_Paid_Amount,
          };
          jsonData.push(obj);
        });

        this.exportToExcelService.exportAsExcelFile(
          jsonData,
          'Receivable Report '
        );
      }
    });
  }
}
